.my-connection-preview {
  background-color: white;
  border-bottom: 1px solid $border;

  .container {
    width: 100%;
    padding: 10px 10px 10px 30px;
    display: flex;
    align-items: center;

    .img-profile {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 15%;

      .img {
        width: 72px;
        height: 72px;
        border-radius: 50%;
        object-fit: cover;
      }
    }

    .fullname {
      width: 65%;

      display: flex;
      flex-direction: column;
    }

    .btns {
      width: 30%;

      display: flex;
      justify-content: space-around;
      align-items: center;

      button {
        color: #0a66c2;
        border: unset;
        background-color: white;
        border-radius: 30px;
        width: 50%;
        padding: 5px 7px;
        border: 1px solid #0a66c2;
        width: 70%;

        &:hover {
          background-color: #70b5f933;
        }
      }

      .dots-icon {
        font-size: 30px;
      }
    }
  }
}
