.img-profile-preview {
  margin-top: 60px;
  background-color: rgb(255, 255, 255);
  height: 50vh;
  position: absolute;

  .bg {
    z-index: 105;
    inset: 0 0 0 0;
    background-color: rgba(0, 0, 0, 0.776);
    position: fixed;
    cursor: pointer;
  }

  .container {
    z-index: 1120;
    position: fixed;
    background-color: #1d2226;
    color: white;
    border-radius: 20px;
    top: 50vh;
    left: 50vw;
    // width: 70vw;
    min-width: 50vw;
    transform: translate(-50%, -50%);
    padding: 20px;
    min-height: min-content;

    .title {
      display: flex;
      justify-content: space-between;
      font-size: 24px;
      text-align: center;
      align-items: center;
      justify-content: center;
      margin: 10px 0;
      position: relative;

      .user-details {
        position: absolute;
        left: 5px;
        display: flex;
        cursor: pointer;
        .img {
          width: 35px;
          height: 35px;
          border-radius: 50%;
          object-fit: cover;
        }
        .fullname {
          display: flex;
          font-size: 1rem;
          margin-left: 5px;
          align-items: center;
        }
      }

      .logo-close {
        cursor: pointer;
        position: absolute;
        right: 5px;
      }
    }

    .body {
      text-align: center;
    }

    .see-post {
      text-align: center;
      margin: 5px 0;
      cursor: pointer;
      p {
        text-decoration: underline;
      }
    }

    .img-container {
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center;
      .img {
        width: 40vw;
        max-height: 40vw;
        // border-radius: 50%;
        object-fit: cover;
      }
    }
  }
}
